@use "sass:map";
@use "../common/_breakpoint" as *;
@use "../common/_functions" as *;
@use "../setting" as *;



$align-self-values: (
  "start": flex-start,
  "end": flex-end,
  "center": center,
  "baseline": baseline,
  "stretch": stretch,
  "auto": auto
) !default;


@include generate-utility-css("self", $align-self-values, null, $align-self-important) using ($value, $important_value) {
  align-self: $value #{$important_value};
}
