@use "sass:map";
@use "../common/_breakpoint" as *;
@use "../common/_functions" as *;
@use "../setting" as *;


$text-align-values: (
  "left": left,
  "center": center,
  "right": right,
  "justify": justify
) !default;

@include generate-utility-css("text-align", $text-align-values, null, $text-align-important) using ($value, $important_value) {
  text-align: $value #{$important_value};
}
