@use "sass:map";
@use "../common/_breakpoint" as *;
@use "../common/_functions" as *;
@use "../setting" as *;

@include generate-utility-css("max-w", $max-width-px, "px", $max-width-important) using ($value, $important_value) {
  max-width: $value + px #{$important_value};
}

@include generate-utility-css("max-w", $max-width-percent, "%", $max-width-important) using ($value, $important_value) {
  max-width: $value * 1% #{$important_value};
}
